.pricing {
  max-width: 1000px;
  margin: 2rem auto;
  padding: 2rem;
  background: #f6f8f7;
  border-radius: 16px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
}
.pricing h1 {
  text-align: center;
  color: #3a4d39;
}
.pricing__subtitle {
  text-align: center;
  color: #4f665a;
  margin-top: 0.5rem;
}
.pricing__plans {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 1.5rem;
  margin-top: 2rem;
}
.pricing__card {
  background: #e8f5e9;
  border-radius: 16px;
  padding: 1.5rem;
  box-shadow: 0 2px 10px rgba(39, 174, 96, 0.1);
  border: 1px solid #c6d4c5;
  display: flex;
  flex-direction: column;
  position: relative;
}
.pricing__card--highlight {
  border-color: #7bb47a;
  box-shadow: 0 6px 18px rgba(39, 174, 96, 0.18);
}
.pricing__card--special {
  background: linear-gradient(135deg, #e8f5e9 0%, #f1f8e9 100%);
  border-color: #ff9800;
  box-shadow: 0 8px 24px rgba(255, 152, 0, 0.2);
  transform: scale(1.02);
}
.pricing__card--special:hover {
  transform: scale(1.03);
  box-shadow: 0 12px 32px rgba(255, 152, 0, 0.25);
}
.pricing__special-badge {
  position: absolute;
  top: -10px;
  right: -10px;
  background: linear-gradient(45deg, #ff9800, #ff5722);
  color: white;
  padding: 0.4rem 0.8rem;
  border-radius: 20px;
  font-weight: 700;
  font-size: 0.8rem;
  box-shadow: 0 2px 8px rgba(255, 152, 0, 0.3);
  animation: pulse 2s infinite;
}
@keyframes pulse {
  0% { transform: scale(1); }
  50% { transform: scale(1.05); }
  100% { transform: scale(1); }
}
.pricing__plan-name {
  margin: 0 0 0.5rem 0;
  color: #2e7d32;
}
.pricing__card--special .pricing__plan-name {
  color: #e65100;
}
.pricing__price {
  display: flex;
  align-items: baseline;
  gap: 0.5rem;
  margin-bottom: 1rem;
}
.pricing__price-amount {
  font-size: 1.8rem;
  font-weight: 800;
  color: #3a4d39;
}
.pricing__card--special .pricing__price-amount {
  color: #e65100;
}
.pricing__price-period {
  color: #4f665a;
}
.pricing__features {
  list-style: none;
  padding: 0;
  margin: 0 0 1rem 0;
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
}
.pricing__features li::before {
  content: '';
  margin-right: 0.5rem;
  color: #7bb47a;
}
.pricing__card--special .pricing__features li::before {
  color: #ff9800;
}
.pricing__btn {
  margin-top: auto;
  background: #7bb47a;
  color: white;
  border: none;
  padding: 0.6rem 1rem;
  border-radius: 8px;
  font-weight: 700;
  cursor: pointer;
  transition: background 0.2s;
}
.pricing__btn:hover { background: #6aa568; }
.pricing__card--special .pricing__btn {
  background: #ff9800;
}
.pricing__card--special .pricing__btn:hover {
  background: #f57c00;
}

/* FAQ */
.pricing__faq {
  margin-top: 2.5rem;
}
.pricing__faq h2 {
  text-align: center;
  color: #3a4d39;
}
.pricing__faq-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}
.pricing__faq-item {
  background: #ffffff;
  border-radius: 12px;
  padding: 1rem;
  border: 1px solid #c6d4c5;
}
.pricing__faq-item h4 {
  margin: 0 0 0.3rem 0;
  color: #2e7d32;
}
.pricing__faq-item p {
  margin: 0;
  color: #4f665a;
}

/* Dark Theme Support */
body.dark .pricing { background: #262b28; }
body.dark .pricing__card { background: #1f2a23; border-color: #4a574a; }
body.dark .pricing__card--special {
  background: linear-gradient(135deg, #1f2a23 0%, #2a332d 100%);
  border-color: #ff9800;
}
body.dark .pricing__price-amount { color: #e8f5e9; }
body.dark .pricing__card--special .pricing__price-amount { color: #ffb74d; }
body.dark .pricing__price-period { color: #b0b0b0; }
body.dark .pricing__faq-item { background: #2a332d; border-color: #4a574a; }

