.newsletter {
  background: #f6f8f7;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
  max-width: 400px;
  margin: 2rem auto;
  text-align: center;
}
.newsletter__form {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  margin-top: 1rem;
}
.newsletter__input {
  padding: 0.5rem 1rem;
  border: 1px solid #b2dfdb;
  border-radius: 6px;
  font-size: 1rem;
  outline: none;
}
.newsletter__button {
  background: #7bb47a;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 0.5rem 1.2rem;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.2s;
}
.newsletter__button:hover {
  background: #5a8d5a;
}
.newsletter__success {
  color: #388e3c;
  margin-top: 1rem;
}
