.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  z-index: 100;
  background: #f6f8f7;
  padding: 1.5rem 0;
  box-shadow: 0 2px 8px rgba(0,0,0,0.03);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  transition: box-shadow 0.2s, background 0.2s;
}
/* .header__logo {
  font-size:10rem;
  font-weight: 600;
  color: #2e7d32;
  text-transform: uppercase;
} */
.header__logo-nav {
  display: flex;
  align-items: center;
  width: 90vw;
  max-width: 1200px;
  justify-content: space-between;
}
.header__logo {
  height: 48px;
}
.header__nav {
  display: flex;
  gap: 1rem;
  align-items: center;
}
.header__link {
  color: #3a4d39;
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 500;
  transition: color 0.2s;
}
.header__link:hover {
  color: #7bb47a;
}

.header__btn {
  background: #7bb47a;
  color: white;
  padding: 0.6rem 1rem;
  border-radius: 8px;
  text-decoration: none;
  font-weight: 600;
  box-shadow: 0 2px 6px rgba(0,0,0,0.08);
  transition: background 0.2s, transform 0.05s;
}
.header__btn:hover {
  background: #6aa568;
}
.header__btn:active {
  transform: translateY(1px);
}

.header__theme-toggle {
  background: transparent;
  border: 1px solid #c6d4c5;
  color: #3a4d39;
  padding: 0.5rem 0.75rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
}
.header__theme-toggle:hover {
  background: #e8f5e9;
}

body {
  padding-top: 90px;
}

/* Mobile Responsive Design */
@media (max-width: 768px) {
  .header {
    padding: 1rem 0;
  }
  
  .header__logo-nav {
    width: 95vw;
    flex-direction: column;
    gap: 1rem;
  }
  
  .header__logo {
    font-size: 1.5rem;
  }
  
  .header__nav {
    gap: 0.5rem;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .header__link {
    font-size: 1rem;
    padding: 0.3rem 0.5rem;
  }
  
  .header__btn {
    padding: 0.5rem 0.8rem;
    font-size: 0.9rem;
  }
  
  .header__theme-toggle {
    padding: 0.4rem 0.6rem;
    font-size: 0.8rem;
  }
  
  body {
    padding-top: 120px;
  }
}

@media (max-width: 480px) {
  .header__nav {
    gap: 0.3rem;
  }
  
  .header__link {
    font-size: 0.9rem;
    padding: 0.2rem 0.4rem;
  }
  
  .header__btn {
    padding: 0.4rem 0.7rem;
    font-size: 0.8rem;
  }
  
  .header__theme-toggle {
    padding: 0.3rem 0.5rem;
    font-size: 0.7rem;
  }
}

/* Tablet Responsive Design */
@media (min-width: 769px) and (max-width: 1024px) {
  .header__logo-nav {
    width: 92vw;
  }
  
  .header__nav {
    gap: 1.5rem;
  }
  
  .header__link {
    font-size: 1.05rem;
  }
}

/* Large Screen Responsive Design */
@media (min-width: 1025px) {
  .header__logo-nav {
    width: 88vw;
  }
  
  .header__nav {
    gap: 2rem;
  }
  
  .header__link {
    font-size: 1.15rem;
  }
  
  .header__btn {
    padding: 0.7rem 1.2rem;
    font-size: 1.1rem;
  }
}

/* Dark theme overrides */
body.dark {
  background: #1f2321;
  color: #e8f5e9;
}
body.dark .header {
  background: #2a2f2c;
  box-shadow: 0 2px 8px rgba(0,0,0,0.4);
}
body.dark .header__link { color: #d7e6d6; }
body.dark .header__link:hover { color: #a3d4a2; }
body.dark .header__theme-toggle {
  border-color: #4a574a;
  color: #e8f5e9;
}
body.dark .header__theme-toggle:hover {
  background: #37413b;
}