.home__hero {
  background: linear-gradient(120deg, #e0f7fa 0%, #e8f5e9 100%);
  padding: 4rem 2rem 3rem 2rem;
  text-align: center;
  border-radius: 0 0 32px 32px;
  position: relative;
}
.home__hero-img-wrap {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
}
.home__hero-img {
  width: 100%;
  max-width: 480px;
  border-radius: 24px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.08);
  object-fit: cover;
}
.home__hero h1 {
  font-size: 2.5rem;
  color: #3a4d39;
  margin-bottom: 0.5rem;
}
.home__slogan {
  font-size: 1.3rem;
  color: #7bb47a;
  margin-bottom: 1rem;
  font-weight: 600;
}
.home__quick-intro {
  font-size: 1.1rem;
  color: #4f665a;
  margin-bottom: 2rem;
}
.home__cta {
  background: #7bb47a;
  color: #fff;
  padding: 0.8rem 2rem;
  border-radius: 8px;
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 500;
  transition: background 0.2s;
}
.home__cta:hover {
  background: #5a8d5a;
}
.home__intro {
  margin: 3rem auto 0 auto;
  max-width: 700px;
  text-align: center;
}
.home__intro h2 {
  color: #3a4d39;
  margin-bottom: 0.5rem;
}
.home__intro p {
  color: #4f665a;
}
.home__popular-classes {
  margin: 3rem auto 0 auto;
  max-width: 1100px;
  text-align: center;
}
.home__popular-classes h2 {
  color: #388e3c;
  margin-bottom: 1.5rem;
}
.home__classes-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 2rem;
}
.home__class-card {
  background: #e8f5e9;
  border-radius: 14px;
  box-shadow: 0 1px 6px rgba(0,0,0,0.07);
  padding: 1.5rem 1rem 1.2rem 1rem;
  text-align: center;
  transition: box-shadow 0.2s;
}
.home__class-card:hover {
  box-shadow: 0 4px 16px rgba(39, 174, 96, 0.12);
}
.home__class-img {
  width: 100%;
  max-width: 180px;
  height: 110px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 0.7rem;
  box-shadow: 0 1px 4px rgba(0,0,0,0.07);
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.home__class-card h3 {
  margin-bottom: 0.5rem;
  color: #3a4d39;
  font-size: 1.1rem;
}
.home__class-card p {
  color: #4f665a;
  font-size: 0.98rem;
}
.home__testimonials {
  margin: 3rem auto 0 auto;
  max-width: 900px;
  text-align: center;
}
.home__testimonials h2 {
  color: #388e3c;
  margin-bottom: 1.5rem;
}
.home__testimonials-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 2rem;
}
.home__testimonial-card {
  background: #f6f8f7;
  border-radius: 14px;
  box-shadow: 0 1px 6px rgba(0,0,0,0.07);
  padding: 1.5rem 1rem 1.2rem 1rem;
  text-align: center;
  transition: box-shadow 0.2s;
}
.home__testimonial-card:hover {
  box-shadow: 0 4px 16px rgba(39, 174, 96, 0.12);
}
.home__testimonial-img {
  width: 70px;
  height: 70px;
  object-fit: cover;
  border-radius: 50%;
  margin-bottom: 0.7rem;
  box-shadow: 0 1px 4px rgba(0,0,0,0.07);
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.home__testimonial-feedback {
  color: #4f665a;
  font-size: 1.02rem;
  margin-bottom: 0.5rem;
  font-style: italic;
}
.home__testimonial-name {
  color: #3a4d39;
  font-weight: 500;
  font-size: 0.98rem;
}
.home__why-serenity {
  margin: 3rem auto 0 auto;
  max-width: 700px;
  text-align: center;
  background: #e8f5e9;
  border-radius: 14px;
  box-shadow: 0 1px 6px rgba(0,0,0,0.07);
  padding: 2rem 1.5rem 1.5rem 1.5rem;
}
.home__why-serenity h2 {
  color: #388e3c;
  margin-bottom: 1rem;
}
.home__why-desc {
  color: #4f665a;
  font-size: 1.08rem;
}
.home__why-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 1.5rem;
  margin-top: 2rem;
}
.home__why-card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 1px 6px rgba(0,0,0,0.07);
  padding: 1.2rem 1rem 1rem 1rem;
  text-align: center;
  transition: box-shadow 0.2s;
}
.home__why-card:hover {
  box-shadow: 0 4px 16px rgba(39, 174, 96, 0.12);
}
.home__why-card h3 {
  color: #3a4d39;
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
}
.home__why-card p {
  color: #4f665a;
  font-size: 0.98rem;
}
.home__why-icon {
  width: 48px;
  height: 48px;
  margin-bottom: 0.7rem;
  display: block;
  margin-left: auto;
  margin-right: auto;
  filter: grayscale(0.2) brightness(0.9);
}
.home__locations {
  margin: 3rem auto 0 auto;
  max-width: 1100px;
  text-align: center;
}
.home__locations h2 {
  color: #388e3c;
  margin-bottom: 1.5rem;
  text-align: center;
}
.home__locations-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 2rem;
}
.home__location-card {
  background: #e8f5e9;
  border-radius: 14px;
  box-shadow: 0 1px 6px rgba(0,0,0,0.07);
  padding: 1.5rem 1rem 1.2rem 1rem;
  text-align: center;
  transition: box-shadow 0.2s;
}
.home__location-card:hover {
  box-shadow: 0 4px 16px rgba(39, 174, 96, 0.12);
}
.home__location-img {
  width: 100%;
  max-width: 280px;
  height: 180px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 0.7rem;
  box-shadow: 0 1px 4px rgba(0,0,0,0.07);
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.home__location-card h3 {
  margin-bottom: 0.5rem;
  color: #3a4d39;
  font-size: 1.1rem;
}
.home__location-card p {
  color: #4f665a;
  font-size: 0.98rem;
}
.home__location-icon {
  width: 20px;
  height: 20px;
  vertical-align: middle;
  margin-right: 0.4rem;
  margin-bottom: 3px;
}
.home__location-maplink {
  color: #388e3c;
  font-weight: 500;
  transition: color 0.2s;
}
.home__location-maplink:hover {
  color: #7bb47a;
}
.home__locations-carousel {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.2rem;
  margin-bottom: 1.5rem;
}
.home__locations-arrow {
  background: #e8f5e9;
  border: none;
  color: #388e3c;
  font-size: 2.2rem;
  border-radius: 50%;
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
  box-shadow: 0 1px 4px rgba(0,0,0,0.07);
}
.home__locations-arrow:hover {
  background: #7bb47a;
  color: #fff;
}
.home__locations-grid.carousel {
  display: flex;
  gap: 2rem;
  justify-content: center;
  width: 700px;
  min-height: 320px;
  transition: opacity 0.25s;
}
.home__locations-grid.carousel.fade {
  opacity: 0.3;
}
@media (max-width: 900px) {
  .home__locations-grid.carousel {
    width: 100vw;
    min-width: 0;
    gap: 1rem;
  }
}

/* Add responsive design to existing Home.css */

/* Mobile Responsive Design */
@media (max-width: 768px) {
  .home {
    padding: 1rem;
  }
  
  .home__hero h1 {
    font-size: 2rem;
    margin-bottom: 1rem;
  }
  
  .home__slogan {
    font-size: 1.1rem;
  }
  
  .home__quick-intro {
    font-size: 0.95rem;
    margin: 1rem 0;
  }
  
  .home__cta {
    padding: 0.8rem 1.5rem;
    font-size: 1rem;
  }
  
  .home__locations-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .home__location-card {
    padding: 1rem;
    width: 100%;
    max-width: 300px;
    margin: 0 auto;
  }
  
  .home__why-cards {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .home__classes-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .home__testimonials-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .home__testimonial-card {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .home__hero h1 {
    font-size: 1.8rem;
  }
  
  .home__slogan {
    font-size: 1rem;
  }
  
  .home__quick-intro {
    font-size: 0.9rem;
  }
  
  .home__cta {
    padding: 0.7rem 1.2rem;
    font-size: 0.9rem;
  }
  
  .home__location-card {
    padding: 0.8rem;
  }
  
  .home__testimonial-card {
    padding: 1rem;
  }
}

/* Tablet Responsive Design */
@media (min-width: 769px) and (max-width: 1024px) {
  .home {
    padding: 2rem;
  }
  
  .home__hero h1 {
    font-size: 2.5rem;
  }
  
  .home__locations-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .home__why-cards {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .home__classes-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .home__testimonials-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

/* Large Screen Responsive Design */
@media (min-width: 1025px) {
  .home {
    padding: 3rem;
  }
  
  .home__hero h1 {
    font-size: 3rem;
  }
  
  .home__locations-grid {
    grid-template-columns: repeat(3, 1fr);
  }
  
  .home__why-cards {
    grid-template-columns: repeat(3, 1fr);
  }
  
  .home__classes-grid {
    grid-template-columns: repeat(4, 1fr);
  }
  
  .home__testimonials-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

/* Extra Large Screen Responsive Design */
@media (min-width: 1440px) {
  .home {
    padding: 4rem;
  }
  
  .home__hero h1 {
    font-size: 3.5rem;
  }
  
  .home__slogan {
    font-size: 1.3rem;
  }
  
  .home__quick-intro {
    font-size: 1.1rem;
  }
}
