.footer {
  background: #e8f5e9;
  color: #3a4d39;
  padding: 2rem 0 1rem 0;
  text-align: center;
  margin-top: 2rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer__content {
  max-width: 1200px;
  margin: 0 auto;
}
.footer__row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 2rem;
  margin-bottom: 1.5rem;
}
.footer__col {
  flex: 1 1 px;
  min-width: 180px;
}
.footer__col h4 {
  margin-bottom: 0.7rem;
  color: #388e3c;
  font-size: 1.08rem;
}
.footer__col.footer__quicknav a{
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.footer__quicknav a {
  display: block;
  margin-bottom: 0.3rem;
  color: #3a4d39;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s;
}
.footer__quicknav a:hover {
  color: #7bb47a;
}
.footer__contactinfo p {
  margin: 0 0 0.3rem 0;
  color: #4f665a;
  font-size: 0.98rem;
}
.footer__links a {
  display: inline-block;
  margin-right: 1rem;
  color: #3a4d39;
  text-decoration: none;
  transition: color 0.2s;
}
.footer__links a:last-child {
  margin-right: 0;
}
.footer__links a:hover {
  color: #7bb47a;
}
.footer__copyright {
  font-size: 0.95rem;
  opacity: 0.7;
}
.footer__privacy {
  margin-left: 0.5rem;
}
.footer__privacy a {
  color: #3a4d39;
  text-decoration: underline;
  font-size: 0.95rem;
  opacity: 0.8;
}
.footer__privacy a:hover {
  color: #7bb47a;
}
.footer__contact-card {
  background: #e8f5e9;
  border-radius: 16px;
  border: 1px solid #b5edb8;
  /* box-shadow: 0 2px 12px rgba(39, 174, 96, 0.10), 0 1px 4px rgba(0,0,0,0.07); */
  padding: 0.5rem 1.5rem 1.5rem 1.5rem;
  /* margin: 2.5rem auto 0 auto; */
  text-align: center;
  max-width: 420px;
}
.footer__contact-card h4 {
  color: #388e3c;
  margin: 1rem 0 0 0;
}
.footer__contact-form {
  display: flex;
  flex-direction: column;
  gap: 0.7rem;
  margin-top: 1rem;
}
.footer__contact-form input,
.footer__contact-form textarea {
  padding: 0.5rem 1rem;
  border: 1px solid #b2dfdb;
  border-radius: 6px;
  font-size: 1rem;
  outline: none;
}
.footer__contact-form button {
  background: #7bb47a;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 0.5rem 1.2rem;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.2s;
}
.footer__contact-form button:hover {
  background: #5a8d5a;
}
.footer__contact-success {
  color: #388e3c;
  margin-top: 1rem;
  font-weight: 500;
}
.footer__map {
  margin-top: 1rem;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 1px 6px rgba(0,0,0,0.07);
}
@media (max-width: 800px) {
  .footer__row {
    flex-direction: column;
    gap: 1.2rem;
  }
  .footer__quicknav a {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    gap: 0.5rem;
  }
  
}
