.blog {
  max-width: 900px;
  margin: 2rem auto;
  padding: 2rem;
  background: #f6f8f7;
  border-radius: 16px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
}
.blog__list {
  list-style: none;
  padding: 0;
  margin: 2rem 0 0 0;
}
.blog__item {
  background: #e8f5e9;
  margin-bottom: 1.5rem;
  padding: 1.5rem 2rem;
  border-radius: 10px;
  box-shadow: 0 1px 4px rgba(0,0,0,0.03);
}
.blog__item h2 {
  color: #388e3c;
  margin-bottom: 0.5rem;
}
.blog__img {
  width: 100%;
  max-width: 340px;
  height: 180px;
  object-fit: cover;
  border-radius: 12px;
  margin-bottom: 1rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.07);
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.blog__tips {
  margin-bottom: 2.5rem;
  background: #e8f5e9;
  border-radius: 16px;
  padding: 2rem 1rem 1.5rem 1rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
}
.blog__tips h2 {
  color: #388e3c;
  margin-bottom: 1.2rem;
  text-align: center;
}
.blog__tips-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 1.5rem;
}
.blog__tip {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 1px 6px rgba(0,0,0,0.07);
  padding: 1rem;
  text-align: center;
}
.blog__tip-img {
  width: 100%;
  max-width: 180px;
  height: 110px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 0.7rem;
  box-shadow: 0 1px 4px rgba(0,0,0,0.07);
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.blog__tip h3 {
  margin-bottom: 0.5rem;
  color: #3a4d39;
  font-size: 1.1rem;
}
.blog__tip p {
  color: #4f665a;
  font-size: 0.98rem;
}
.blog__featured, .blog__latest {
  margin-bottom: 2.5rem;
  background: #e8f5e9;
  border-radius: 16px;
  padding: 2rem 1rem 1.5rem 1rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
}
.blog__featured h2, .blog__latest h2 {
  color: #388e3c;
  margin-bottom: 1.2rem;
  text-align: center;
}
.blog__articles-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 1.5rem;
}
.blog__article-card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 1px 6px rgba(0,0,0,0.07);
  padding: 1rem;
  text-align: center;
  transition: box-shadow 0.2s;
}
.blog__article-card:hover {
  box-shadow: 0 4px 16px rgba(39, 174, 96, 0.12);
}
.blog__article-img {
  width: 100%;
  max-width: 180px;
  height: 110px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 0.7rem;
  box-shadow: 0 1px 4px rgba(0,0,0,0.07);
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.blog__article-card h3 {
  margin-bottom: 0.5rem;
  color: #3a4d39;
  font-size: 1.1rem;
}
.blog__article-card p {
  color: #4f665a;
  font-size: 0.98rem;
}
.blog__filters {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  justify-content: center;
  margin: 2rem 0 2.5rem 0;
}
.blog__filter-btn {
  background: #e8f5e9;
  color: #388e3c;
  border: none;
  border-radius: 6px;
  padding: 0.6rem 1.4rem;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
}
.blog__filter-btn.active, .blog__filter-btn:hover {
  background: #7bb47a;
  color: #fff;
}
.blog__intro-card {
  background: url('https://images.unsplash.com/photo-1506744038136-46273834b3fb?auto=format&fit=crop&w=800&q=80') center/cover no-repeat;
  position: relative;
  border-radius: 16px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
  padding: 2.2rem 1.5rem 1.5rem 1.5rem;
  margin-bottom: 2.5rem;
  text-align: center;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
}
.blog__intro-card::before {
  content: '';
  position: absolute;
  inset: 0;
  background: rgba(232, 245, 233, 0.92);
  z-index: 0;
}
.blog__intro-card > * {
  position: relative;
  z-index: 1;
}
.blog__intro-card h1 {
  color: #388e3c;
  margin-bottom: 1rem;
}
.blog__intro-desc {
  color: #4f665a;
  font-size: 1.08rem;
  margin-bottom: 1.2rem;
}
.blog__intro-highlights {
  display: flex;
  gap: 1.2rem;
  justify-content: center;
  margin-top: 0.5rem;
}
.blog__intro-highlights span {
  background: #7bb47a;
  color: #fff;
  border-radius: 8px;
  padding: 0.4rem 1.1rem;
  font-size: 1rem;
  font-weight: 500;
  box-shadow: 0 1px 4px rgba(0,0,0,0.07);
}
.blog__newsletter-card {
  background: #e8f5e9;
  border-radius: 16px;
  box-shadow: 0 2px 12px rgba(39, 174, 96, 0.10), 0 1px 4px rgba(0,0,0,0.07);
  padding: 2.2rem 1.5rem 1.5rem 1.5rem;
  margin: 2.5rem auto 0 auto;
  text-align: center;
  max-width: 600px;
}
.blog__newsletter-card h2 {
  color: #388e3c;
  margin-bottom: 1rem;
}
.blog__newsletter-card p {
  color: #4f665a;
  font-size: 1.08rem;
  margin-bottom: 1.2rem;
}
.blog__newsletter-form {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  margin-top: 1rem;
}
.blog__newsletter-input {
  padding: 0.5rem 1rem;
  border: 1px solid #b2dfdb;
  border-radius: 6px;
  font-size: 1rem;
  outline: none;
}
.blog__newsletter-btn {
  background: #7bb47a;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 0.5rem 1.2rem;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.2s;
}
.blog__newsletter-btn:hover {
  background: #5a8d5a;
}
.blog__newsletter-success {
  color: #388e3c;
  margin-top: 1rem;
  font-weight: 500;
}
