.classes {
  max-width: 900px;
  margin: 2rem auto;
  padding: 2rem;
  background: #f6f8f7;
  border-radius: 16px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
}

/* Weekly Schedule Section */
.classes__schedule {
  margin-bottom: 3rem;
}
.classes__book-btn-link{
  text-decoration: none;
  color: #2e7d32;
}

.classes__schedule h2 {
  text-align: center;
  color: #3a4d39;
  margin-bottom: 2rem;
  font-size: 2rem;
}

.classes__schedule-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.classes__day-card {
  background: #e8f5e9;
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 2px 8px rgba(39, 174, 96, 0.1);
  border: 1px solid #c6d4c5;
}

.classes__day-title {
  color: #2e7d32;
  margin: 0 0 1rem 0;
  font-size: 1.3rem;
  font-weight: 600;
  text-align: center;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid #7bb47a;
}

.classes__day-classes {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.classes__schedule-item {
  background: white;
  padding: 1rem;
  border-radius: 8px;
  border-left: 4px solid #7bb47a;
  box-shadow: 0 1px 4px rgba(0,0,0,0.05);
}

.classes__schedule-time {
  color: #7bb47a;
  font-weight: 600;
  font-size: 0.9rem;
  margin-bottom: 0.3rem;
}

.classes__schedule-name {
  color: #3a4d39;
  font-weight: 600;
  font-size: 1.1rem;
  margin-bottom: 0.2rem;
}

.classes__schedule-instructor {
  color: #666;
  font-size: 0.9rem;
  font-style: italic;
  margin-bottom: 0.8rem;
}

.classes__book-btn {
  background: #7bb47a;
  color: white;
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  font-weight: 600;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background 0.2s, transform 0.1s;
  width: 100%;
  margin-top: 0.5rem;
}

.classes__book-btn:hover {
  background: #6aa568;
  transform: translateY(-1px);
}

.classes__book-btn:active {
  transform: translateY(0);
}

.classes__list {
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
  margin-top: 2rem;
}

.classes__item {
  background: #e8f5e9;
  padding: 1.5rem 2rem;
  border-radius: 10px;
  flex: 1 1 250px;
  min-width: 220px;
  box-shadow: 0 1px 4px rgba(0,0,0,0.03);
  display: flex;
  flex-direction: column;
}

.classes__item h2 {
  color: #388e3c;
  margin-bottom: 0.5rem;
}

.classes__item p {
  margin-bottom: 0.5rem;
}

.classes__book-btn--card {
  margin-top: auto;
  margin-top: 1rem;
  padding: 0.7rem 1.2rem;
  font-size: 1rem;
}

.classes__img {
  width: 100%;
  max-width: 180px;
  height: 110px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 0.7rem;
  box-shadow: 0 1px 4px rgba(0,0,0,0.07);
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/* Responsive Design */
@media (max-width: 768px) {
  .classes__schedule-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .classes__day-card {
    padding: 1rem;
  }
  
  .classes__schedule-item {
    padding: 0.8rem;
  }
  
  .classes__book-btn {
    padding: 0.6rem 1rem;
    font-size: 1rem;
  }
}

/* Dark Theme Support */
body.dark .classes {
  background: #262b28;
}

body.dark .classes__day-card {
  background: #1f2a23;
  border-color: #4a574a;
}

body.dark .classes__schedule-item {
  background: #2a332d;
  border-left-color: #7bb47a;
}

body.dark .classes__day-title {
  color: #a3d4a2;
  border-bottom-color: #7bb47a;
}

body.dark .classes__schedule-name {
  color: #e8f5e9;
}

body.dark .classes__schedule-instructor {
  color: #b0b0b0;
}

body.dark .classes__item {
  background: #1f2a23;
}
