.about {
  max-width: 1000px;
  margin: 2.5rem auto;
  padding: 2.5rem 1.5rem;
  background: linear-gradient(120deg, #e0f7fa 0%, #e8f5e9 100%);
  border-radius: 24px;
  box-shadow: 0 4px 32px rgba(39, 174, 96, 0.08), 0 1.5px 8px rgba(0,0,0,0.04);
  position: relative;
}
.about__philosophy {
  margin-bottom: 2.5rem;
  text-align: center;
}
.about__philosophy h1 {
  color: #388e3c;
  font-size: 2.2rem;
  margin-bottom: 1.2rem;
  letter-spacing: 1px;
}
.about__philosophy p {
  color: #4f665a;
  font-size: 1.08rem;
  margin-bottom: 0.7rem;
  line-height: 1.7;
}
.about__styles {
  margin-bottom: 2.5rem;
}
.about__styles h2 {
  color: #388e3c;
  text-align: center;
  margin-bottom: 1.2rem;
  font-size: 1.4rem;
  letter-spacing: 0.5px;
}
.about__styles-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}
.about__style-card {
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 2px 12px rgba(39, 174, 96, 0.08), 0 1px 4px rgba(0,0,0,0.07);
  padding: 1.5rem 1.2rem 1.2rem 1.2rem;
  text-align: center;
  transition: box-shadow 0.2s, transform 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;
  opacity: 0;
  transform: translateY(30px);
  animation: fadeInUp 0.8s forwards;
}
.about__style-card:hover {
  box-shadow: 0 8px 32px rgba(39, 174, 96, 0.16), 0 2px 8px rgba(0,0,0,0.09);
  transform: translateY(-6px) scale(1.03);
}
.about__style-card:nth-child(2) { animation-delay: 0.15s; }
.about__style-card:nth-child(3) { animation-delay: 0.3s; }
@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.about__style-img {
  width: 100%;
  max-width: 140px;
  height: 90px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 0.9rem;
  box-shadow: 0 2px 8px rgba(39, 174, 96, 0.10);
  display: block;
}
.about__style-card h3 {
  color: #3a4d39;
  margin-bottom: 0.5rem;
  font-size: 1.13rem;
  letter-spacing: 0.5px;
}
.about__style-card p {
  color: #4f665a;
  font-size: 1.01rem;
  line-height: 1.6;
}
.about__instructors {
  margin-top: 2.5rem;
}
.about__instructors h2 {
  color: #388e3c;
  text-align: center;
  margin-bottom: 1.2rem;
  font-size: 1.3rem;
  letter-spacing: 0.5px;
}
.about__bios {
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
  justify-content: center;
}
.about__bio {
  background: #f6f8f7;
  padding: 1.2rem 2rem;
  border-radius: 14px;
  flex: 1 1 240px;
  min-width: 220px;
  box-shadow: 0 1px 6px rgba(0,0,0,0.04);
  margin-bottom: 1.2rem;
  text-align: center;
  transition: box-shadow 0.2s, transform 0.2s;
}
.about__bio:hover {
  box-shadow: 0 4px 16px rgba(39, 174, 96, 0.10);
  transform: translateY(-4px) scale(1.03);
}
.about__bio-img {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 0.7rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.about__bio h3 {
  color: #388e3c;
  margin-bottom: 0.4rem;
  font-size: 1.08rem;
}
.about__bio p {
  color: #4f665a;
  font-size: 0.98rem;
  line-height: 1.5;
}
.about__highlights {
  display: flex;
  gap: 2rem;
  margin-bottom: 2.5rem;
  justify-content: center;
  flex-wrap: wrap;
}
.about__highlight-card {
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 2px 12px rgba(39, 174, 96, 0.10), 0 1px 4px rgba(0,0,0,0.07);
  padding: 2rem 1.5rem 1.2rem 1.5rem;
  min-width: 260px;
  max-width: 420px;
  flex: 1 1 320px;
  text-align: center;
  transition: box-shadow 0.2s, transform 0.2s;
  margin-bottom: 1rem;
}
.about__highlight-card:hover {
  box-shadow: 0 8px 32px rgba(39, 174, 96, 0.16), 0 2px 8px rgba(0,0,0,0.09);
  transform: translateY(-6px) scale(1.03);
}
.about__highlight-card h2 {
  color: #388e3c;
  font-size: 1.3rem;
  margin-bottom: 0.7rem;
  letter-spacing: 0.5px;
}
.about__highlight-card p {
  color: #4f665a;
  font-size: 1.05rem;
  line-height: 1.7;
}
.about__standfor {
  margin-bottom: 2.5rem;
}
.about__standfor h2 {
  color: #388e3c;
  text-align: center;
  margin-bottom: 1.2rem;
  font-size: 1.3rem;
  letter-spacing: 0.5px;
}
.about__standfor-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 1.5rem;
  margin-top: 1.5rem;
}
.about__standfor-card {
  background: #fff;
  border-radius: 14px;
  box-shadow: 0 2px 12px rgba(39, 174, 96, 0.10), 0 1px 4px rgba(0,0,0,0.07);
  padding: 1.2rem 1rem 1rem 1rem;
  text-align: center;
  transition: box-shadow 0.2s, transform 0.2s;
}
.about__standfor-card:hover {
  box-shadow: 0 8px 32px rgba(39, 174, 96, 0.16), 0 2px 8px rgba(0,0,0,0.09);
  transform: translateY(-6px) scale(1.03);
}
.about__standfor-card h3 {
  color: #3a4d39;
  margin-bottom: 0.5rem;
  font-size: 1.08rem;
  letter-spacing: 0.5px;
}
.about__standfor-card p {
  color: #4f665a;
  font-size: 0.97rem;
  line-height: 1.5;
}
.about__standfor-icon {
  width: 44px;
  height: 44px;
  margin-bottom: 0.7rem;
  display: block;
  margin-left: auto;
  margin-right: auto;
  filter: grayscale(0.1) brightness(0.95);
}
.about__cta {
  background: #e8f5e9;
  border-radius: 16px;
  box-shadow: 0 2px 12px rgba(39, 174, 96, 0.10), 0 1px 4px rgba(0,0,0,0.07);
  padding: 2.2rem 1.5rem 1.5rem 1.5rem;
  margin: 2.5rem auto 0 auto;
  text-align: center;
  max-width: 600px;
}
.about__cta h2 {
  color: #388e3c;
  margin-bottom: 1rem;
}
.about__cta p {
  color: #4f665a;
  font-size: 1.08rem;
  margin-bottom: 0.5rem;
}
.about__cta-btns {
  display: flex;
  gap: 1.2rem;
  justify-content: center;
  margin-top: 1.2rem;
}
.about__cta-link {
  color: #fff;
  background: #7bb47a;
  border-radius: 8px;
  padding: 0.6rem 1.4rem;
  font-size: 1rem;
  font-weight: 500;
  text-decoration: none;
  transition: background 0.2s;
  display: inline-block;
}
.about__cta-link:hover {
  background: #5a8d5a;
}
.about__intro-card {
  background: url('https://images.unsplash.com/photo-1506744038136-46273834b3fb?auto=format&fit=crop&w=900&q=80') center/cover no-repeat;
  position: relative;
  border-radius: 20px;
  box-shadow: 0 2px 12px rgba(39, 174, 96, 0.10), 0 1px 4px rgba(0,0,0,0.07);
  padding: 2.5rem 1.5rem 2rem 1.5rem;
  margin-bottom: 2.5rem;
  text-align: center;
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
}
.about__intro-card::before {
  content: '';
  position: absolute;
  inset: 0;
  background: rgba(232, 245, 233, 0.92);
  z-index: 0;
}
.about__intro-card > * {
  position: relative;
  z-index: 1;
}
.about__intro-card h1 {
  color: #388e3c;
  font-size: 2.1rem;
  margin-bottom: 1rem;
  letter-spacing: 1px;
}
.about__intro-card p {
  color: #4f665a;
  font-size: 1.13rem;
  margin-bottom: 0.7rem;
  line-height: 1.7;
}

/* CEO Section */
.about__ceo {
  margin: 3rem 0;
}

.about__ceo h2 {
  text-align: center;
  color: #3a4d39;
  margin-bottom: 2rem;
  font-size: 2rem;
}

.about__ceo-card {
  background: #e8f5e9;
  border-radius: 20px;
  padding: 2.5rem;
  box-shadow: 0 4px 20px rgba(39, 174, 96, 0.15);
  display: flex;
  gap: 2.5rem;
  align-items: flex-start;
  max-width: 900px;
  margin: 0 auto;
}

.about__ceo-photo {
  flex-shrink: 0;
}

.about__ceo-img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid #7bb47a;
  box-shadow: 0 4px 16px rgba(39, 174, 96, 0.2);
}

.about__ceo-content {
  flex: 1;
}

.about__ceo-content h3 {
  color: #2e7d32;
  font-size: 1.8rem;
  margin: 0 0 0.5rem 0;
  font-weight: 600;
}

.about__ceo-title {
  color: #7bb47a;
  font-size: 1.1rem;
  font-weight: 600;
  margin: 0 0 1.5rem 0;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.about__ceo-bio {
  color: #4f665a;
  line-height: 1.7;
  margin-bottom: 1rem;
  font-size: 1rem;
}

.about__ceo-quote {
  background: white;
  border-left: 4px solid #7bb47a;
  padding: 1.5rem;
  margin-top: 1.5rem;
  border-radius: 0 8px 8px 0;
  box-shadow: 0 2px 8px rgba(0,0,0,0.05);
}

.about__ceo-quote p {
  color: #3a4d39;
  font-style: italic;
  font-size: 1.1rem;
  margin: 0;
  line-height: 1.6;
}

/* Responsive Design for CEO Section */
@media (max-width: 768px) {
  .about__ceo-card {
    flex-direction: column;
    text-align: center;
    padding: 2rem;
    gap: 2rem;
  }
  
  .about__ceo-img {
    width: 150px;
    height: 150px;
  }
  
  .about__ceo-content h3 {
    font-size: 1.5rem;
  }
  
  .about__ceo-quote {
    margin-top: 1rem;
  }
}

/* Dark Theme Support for CEO Section */
body.dark .about__ceo-card {
  background: #1f2a23;
  box-shadow: 0 4px 20px rgba(0,0,0,0.3);
}

body.dark .about__ceo-content h3 {
  color: #a3d4a2;
}

body.dark .about__ceo-title {
  color: #7bb47a;
}

body.dark .about__ceo-bio {
  color: #e8f5e9;
}

body.dark .about__ceo-quote {
  background: #2a332d;
  border-left-color: #7bb47a;
}

body.dark .about__ceo-quote p {
  color: #e8f5e9;
}

/* Add responsive design to existing About.css */

/* Mobile Responsive Design */
@media (max-width: 768px) {
  .about {
    padding: 1rem;
  }
  
  .about__intro-card {
    padding: 1.5rem;
    margin-bottom: 2rem;
  }
  
  .about__intro-card h1 {
    font-size: 2rem;
  }
  
  .about__intro-card p {
    font-size: 1rem;
  }
  
  .about__highlights {
    flex-direction: column;
    gap: 1.5rem;
  }
  
  .about__highlight-card {
    padding: 1.5rem;
  }
  
  .about__highlight-card h2 {
    font-size: 1.5rem;
  }
  
  .about__standfor-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .about__standfor-card {
    padding: 1.5rem;
  }
  
  .about__styles-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .about__style-card {
    padding: 1.5rem;
  }
  
  .about__bios {
    flex-direction: column;
    gap: 2rem;
  }
  
  .about__bio {
    padding: 1.5rem;
  }
  
  .about__ceo-card {
    flex-direction: column;
    text-align: center;
    padding: 2rem;
    gap: 2rem;
  }
  
  .about__ceo-img {
    width: 150px;
    height: 150px;
  }
  
  .about__ceo-content h3 {
    font-size: 1.5rem;
  }
  
  .about__cta {
    padding: 2rem 1.5rem;
  }
  
  .about__cta h2 {
    font-size: 1.8rem;
  }
  
  .about__cta-btns {
    flex-direction: column;
    gap: 1rem;
  }
}

@media (max-width: 480px) {
  .about__intro-card h1 {
    font-size: 1.8rem;
  }
  
  .about__intro-card p {
    font-size: 0.9rem;
  }
  
  .about__highlight-card h2 {
    font-size: 1.3rem;
  }
  
  .about__standfor-card h3 {
    font-size: 1.2rem;
  }
  
  .about__style-card h3 {
    font-size: 1.2rem;
  }
  
  .about__bio h3 {
    font-size: 1.3rem;
  }
  
  .about__ceo-content h3 {
    font-size: 1.3rem;
  }
  
  .about__cta h2 {
    font-size: 1.5rem;
  }
}

/* Tablet Responsive Design */
@media (min-width: 769px) and (max-width: 1024px) {
  .about {
    padding: 2rem;
  }
  
  .about__intro-card {
    padding: 2.5rem;
  }
  
  .about__intro-card h1 {
    font-size: 2.5rem;
  }
  
  .about__highlights {
    gap: 2rem;
  }
  
  .about__standfor-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .about__styles-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .about__bios {
    gap: 3rem;
  }
  
  .about__ceo-card {
    gap: 3rem;
  }
  
  .about__cta {
    padding: 3rem 2rem;
  }
}

/* Large Screen Responsive Design */
@media (min-width: 1025px) {
  .about {
    padding: 3rem;
  }
  
  .about__intro-card {
    padding: 3rem;
  }
  
  .about__intro-card h1 {
    font-size: 3rem;
  }
  
  .about__intro-card p {
    font-size: 1.2rem;
  }
  
  .about__highlights {
    gap: 3rem;
  }
  
  .about__highlight-card {
    padding: 2.5rem;
  }
  
  .about__highlight-card h2 {
    font-size: 2rem;
  }
  
  .about__standfor-grid {
    grid-template-columns: repeat(4, 1fr);
  }
  
  .about__styles-grid {
    grid-template-columns: repeat(3, 1fr);
  }
  
  .about__bios {
    gap: 4rem;
  }
  
  .about__bio {
    padding: 2.5rem;
  }
  
  .about__ceo-card {
    gap: 3rem;
  }
  
  .about__cta {
    padding: 4rem 3rem;
  }
  
  .about__cta h2 {
    font-size: 2.5rem;
  }
}

/* Extra Large Screen Responsive Design */
@media (min-width: 1440px) {
  .about {
    padding: 4rem;
  }
  
  .about__intro-card {
    padding: 4rem;
  }
  
  .about__intro-card h1 {
    font-size: 3.5rem;
  }
  
  .about__intro-card p {
    font-size: 1.3rem;
  }
  
  .about__highlight-card h2 {
    font-size: 2.2rem;
  }
  
  .about__cta h2 {
    font-size: 3rem;
  }
}
