.contact {
  max-width: 700px;
  margin: 2rem auto;
  padding: 2rem;
  background: #f6f8f7;
  border-radius: 16px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
}
.contact__info {
  margin-bottom: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.contact__socials-title {
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 1rem;
  text-align: center;
}
.contact__whatsapp {
  color: #25D366;
  font-weight: 500;
  text-decoration: none;
  margin-left: 0.5rem;
}
.contact__socials {
  margin: 1rem 0;
  display: flex;
  gap: 1.5rem;
}
.contact__socials a {
  color: #3a4d39;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s;
}
.contact__socials a:hover {
  color: #7bb47a;
}
.contact__map {
  margin-top: 1rem;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 1px 6px rgba(0,0,0,0.07);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.contact__form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.contact__form input,
.contact__form textarea {
  padding: 0.7rem 1rem;
  border: 1px solid #b2dfdb;
  border-radius: 6px;
  font-size: 1rem;
  outline: none;
}
.contact__form button {
  background: #7bb47a;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 0.7rem 1.2rem;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.2s;
}
.contact__form button:hover {
  background: #5a8d5a;
}
.contact__success {
  color: #388e3c;
  margin-top: 1rem;
}
.contact__inline-cards {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
  margin-top: 2rem;
}
.contact__card {
  background: #e8f5e9;
  border-radius: 16px;
  box-shadow: 0 2px 12px rgba(39, 174, 96, 0.10), 0 1px 4px rgba(0,0,0,0.07);
  padding: 2rem 1.5rem 1.5rem 1.5rem;
  margin-bottom: 0;
  height: fit-content;
  display: grid;
  grid-column: 1/3;

}
.contact__form-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* Make the info card span full width for the map */
.contact__info {
  grid-column: 1 / -1;
  margin-bottom: 0;
}

/* Get In Touch Card */
.contact__touch-card h3 {
  margin-top: 0;
  margin-bottom: 1.5rem;
  color: #2e7d32;
  text-align: center;
}

.contact__touch-methods {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  margin-bottom: 1.5rem;
}

.contact__touch-item {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
  padding: 0.8rem;
  background: white;
  border-radius: 8px;
  border-left: 3px solid #7bb47a;
}

.contact__touch-icon {
  font-size: 1.5rem;
  flex-shrink: 0;
  margin-top: 0.1rem;
}

.contact__touch-item strong {
  color: #3a4d39;
  display: block;
  margin-bottom: 0.2rem;
}

.contact__touch-item p {
  margin: 0 0 0.2rem 0;
  color: #7bb47a;
  font-weight: 500;
}

.contact__touch-item small {
  color: #666;
  font-size: 0.85rem;
}

.contact__hours {
  margin-bottom: 1.5rem;
  padding: 1rem;
  background: white;
  border-radius: 8px;
  border: 1px solid #c6d4c5;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-left: 3px solid #7bb47a
}

.contact__hours h4 {
  margin: 0 0 0.8rem 0;
  color: #2e7d32;
  font-size: 1.1rem;
}

.contact__hours-grid {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  font-size: 0.9rem;
}

.contact__hours-grid span {
  font-weight: 600;
  color: #3a4d39;
  min-width: 100px;
  display: inline-block;
}

.contact__touch-actions {
  display: flex;
  gap: 0.75rem;
  flex-wrap: wrap;
}

.contact__touch-btn {
  background: #7bb47a;
  color: #fff;
  text-decoration: none;
  padding: 0.6rem 1rem;
  border-radius: 8px;
  font-weight: 600;
  font-size: 0.9rem;
  transition: background 0.2s;
  flex: 1;
  text-align: center;
}

.contact__touch-btn:hover { 
  background: #6aa568; 
}

.contact__touch-btn--secondary {
  background: transparent;
  border: 1px solid #c6d4c5;
  color: #3a4d39;
}

.contact__touch-btn--secondary:hover {
  background: #e3f2e1;
}

/* Join Now card */
.contact__join-card h3 {
  margin-top: 0;
  margin-bottom: 0.5rem;
}
.contact__join-list {
  margin: 0.5rem 0 1rem;
  padding-left: 1.2rem;
  line-height: 1.6;
}
.contact__join-actions {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.75rem;
  flex-wrap: wrap;
}
.contact__join-btn {
  background: #7bb47a;
  color: #fff;
  text-decoration: none;
  padding: 0.6rem 1rem;
  border-radius: 8px;
  font-weight: 600;
  transition: background 0.2s;
}
.contact__join-btn:hover { background: #6aa568; }
.contact__join-btn--secondary {
  background: transparent;
  border: 1px solid #c6d4c5;
  color: #3a4d39;
}
.contact__join-btn--secondary:hover {
  background: #e3f2e1;
}
@media (max-width: 900px) {
  .contact__inline-cards {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .contact__info {
    grid-column: 1;
  }
  
  .contact__card {
    margin-bottom: 0;
  }
}

/* Dark mode adjustments */
body.dark .contact { background: #262b28; }
body.dark .contact__card { background: #1f2a23; box-shadow: 0 2px 12px rgba(0,0,0,0.25), 0 1px 4px rgba(0,0,0,0.25); }
body.dark .contact__socials a { color: #d7e6d6; }
body.dark .contact__socials a:hover { color: #a3d4a2; }
body.dark .contact__join-btn--secondary { border-color: #4a574a; color: #e8f5e9; }
body.dark .contact__join-btn--secondary:hover { background: #2a332d; }

/* Dark theme for Get In Touch card */
body.dark .contact__touch-item {
  background: #2a332d;
  border-left-color: #7bb47a;
}

body.dark .contact__touch-item strong {
  color: #e8f5e9;
}

body.dark .contact__touch-item p {
  color: #a3d4a2;
}

body.dark .contact__touch-item small {
  color: #b0b0b0;
}

body.dark .contact__hours {
  background: #2a332d;
  border-color: #4a574a;
}

body.dark .contact__hours h4 {
  color: #a3d4a2;
}

body.dark .contact__hours-grid span {
  color: #e8f5e9;
}

body.dark .contact__touch-btn--secondary {
  border-color: #4a574a;
  color: #e8f5e9;
}

body.dark .contact__touch-btn--secondary:hover {
  background: #2a332d;
}

/* Add responsive design to existing Contact.css */

/* Mobile Responsive Design */
@media (max-width: 768px) {
  .contact {
    padding: 1rem;
    margin: 1rem auto;
  }
  
  .contact h1 {
    font-size: 2rem;
  }
  
  .contact__inline-cards {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
  
  .contact__card {
    padding: 1.5rem;
    min-width: 0;
  }
  
  .contact__info {
    margin-bottom: 0;
  }
  
  .contact__socials {
    gap: 1rem;
    justify-content: center;
  }
  
  .contact__touch-methods {
    gap: 1rem;
  }
  
  .contact__touch-item {
    padding: 0.6rem;
  }
  
  .contact__touch-icon {
    font-size: 1.3rem;
  }
  
  .contact__hours {
    padding: 0.8rem;
  }
  
  .contact__touch-actions {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .contact__join-actions {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .contact__form input,
  .contact__form textarea {
    padding: 0.6rem 0.8rem;
    font-size: 0.95rem;
  }
  
  .contact__form button {
    padding: 0.6rem 1rem;
    font-size: 0.95rem;
  }
}

@media (max-width: 480px) {
  .contact {
    padding: 0.8rem;
    margin: 0.8rem auto;
  }
  
  .contact h1 {
    font-size: 1.8rem;
  }
  
  .contact__card {
    padding: 1rem;
  }
  
  .contact__touch-item {
    padding: 0.5rem;
  }
  
  .contact__touch-icon {
    font-size: 1.2rem;
  }
  
  .contact__hours {
    padding: 0.6rem;
  }
  
  .contact__form input,
  .contact__form textarea {
    padding: 0.5rem 0.7rem;
    font-size: 0.9rem;
  }
  
  .contact__form button {
    padding: 0.5rem 0.8rem;
    font-size: 0.9rem;
  }
}

/* Tablet Responsive Design */
@media (min-width: 769px) and (max-width: 1024px) {
  .contact {
    padding: 2rem;
    margin: 2rem auto;
  }
  
  .contact h1 {
    font-size: 2.5rem;
  }
  
  .contact__inline-cards {
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
  
  .contact__card {
    padding: 2rem;
  }
  
  .contact__touch-methods {
    gap: 1.2rem;
  }
  
  .contact__touch-item {
    padding: 1rem;
  }
  
  .contact__hours {
    padding: 1.2rem;
  }
  
  .contact__form input,
  .contact__form textarea {
    padding: 0.8rem 1.2rem;
    font-size: 1.05rem;
  }
  
  .contact__form button {
    padding: 0.8rem 1.5rem;
    font-size: 1.05rem;
  }
}

/* Large Screen Responsive Design */
@media (min-width: 1025px) {
  .contact {
    padding: 3rem;
    margin: 3rem auto;
  }
  
  .contact h1 {
    font-size: 3rem;
  }
  
  .contact__inline-cards {
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
  
  .contact__card {
    padding: 2.5rem;
  }
  
  .contact__touch-methods {
    gap: 1.5rem;
  }
  
  .contact__touch-item {
    padding: 1.2rem;
  }
  
  .contact__hours {
    padding: 1.5rem;
  }
  
  .contact__form input,
  .contact__form textarea {
    padding: 1rem 1.5rem;
    font-size: 1.1rem;
  }
  
  .contact__form button {
    padding: 1rem 2rem;
    font-size: 1.1rem;
  }
}

/* Extra Large Screen Responsive Design */
@media (min-width: 1440px) {
  .contact {
    padding: 4rem;
    margin: 4rem auto;
  }
  
  .contact h1 {
    font-size: 3.5rem;
  }
  
  .contact__inline-cards {
    gap: 4rem;
  }
  
  .contact__card {
    padding: 3rem;
  }
  
  .contact__touch-methods {
    gap: 2rem;
  }
  
  .contact__touch-item {
    padding: 1.5rem;
  }
  
  .contact__hours {
    padding: 2rem;
  }
  
  .contact__form input,
  .contact__form textarea {
    padding: 1.2rem 2rem;
    font-size: 1.2rem;
  }
  
  .contact__form button {
    padding: 1.2rem 2.5rem;
    font-size: 1.2rem;
  }
}
