
/* Responsive Design - Mobile First Approach */
@media (max-width: 768px) {
  body {
    font-size: 14px;
  }
  
  .main-content {
    padding: 0 1rem;
  }
  .newsletter__input {
    padding: 0.5rem 0.5rem;
    width: 100%;
    border: 1px solid #b2dfdb;
    border-radius: 6px;
    font-size: 1rem;
    outline: none;
}

}

@media (max-width: 480px) {
  body {
    font-size: 13px;
  }
  
  .main-content {
    padding: 0 0.5rem;
  }
  .footer__col.footer__quicknav a{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
    gap: 0.5rem;
  }
  .footer__quicknav a {
    flex-wrap: wrap;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    margin-bottom: 0.3rem;
    color: #3a4d39;
    text-decoration: none;
    font-weight: 500;
    transition: color 0.2s;
  }
  .footer__quicknav a:hover {
    color: #7bb47a;
  }
}

/* Tablet and Small Laptop */
@media (min-width: 769px) and (max-width: 1024px) {
  body {
    font-size: 16px;
  }
  
  .main-content {
    padding: 0 2rem;
  }
}

/* Large Laptop and Desktop */
@media (min-width: 1025px) {
  body {
    font-size: 18px;
  }
  
  .main-content {
    padding: 0 3rem;
  }
}

/* Extra Large Screens */
@media (min-width: 1440px) {
  body {
    font-size: 20px;
  }
  
  .main-content {
    padding: 0 4rem;
  }
}
